<template>
  <div class="videoList">
    <div class="list" v-for="(item,index) in video" @click="toMvList(item.video,item.img)">
        <img v-lazy="'static/img/'+item.img" alt=""/>
		<p> <span>{{index}}</span> <span>{{item.title}}</span></p>
    </div>
</div>
</template>

<script>
import '@/assets/video.css';
import axios from 'axios';
export default {
  name: 'm_video',
  data () {
    return {
       video:[]
    }
  },
	components:{
		
	},
	mounted(){
		axios.get('/goods').then(res=>{
		const	result=res.data.data.result;
		 this.video=result;
		})
	},
	methods:{
		toMvList(src,post){
			this.$router.push({name:'mvlist', params:{src:src,post:post}});
		}
	}
}
</script>



