import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import MV from '@/components/m_video'
import List from '@/views/movielist'
import detail from '@/views/movieDetail'
import mvlist from '@/views/mvlist'
import test from '@/views/test'

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component:Index
			},
			{
				path: '/MV',
				name: 'MV',
				component:MV
				},
			{
				path: '/list',
				name: 'list',
				component:List
				},
				{
					path: '/detail',
					name: 'detail',
					component:detail
					},
					{
						path: '/test',
						name: 'test',
						component:test
						},
						{
							path: '/mvlist',
							name: 'mvlist',
							component:mvlist
							}
  ]
})



