<template>
  <div class="movie">
		
  		<div :title="item.allTilte" class="pic" v-for="(item,i) in post" @click="detail(item.movieId)">
  				<img class="poster"  v-lazy="item.coverageUrl" alt="">
  				<h3>{{item.title}}</h3>
  				<div class="rate">
  				<img  v-for="star in item.stars" v-if="star==2"  src="../../static/star1.png" alt="">
  				<img v-for="star in item.stars" v-if="star==1"  src="../../static/star2.png" alt="">
  				<img v-for="star in item.stars" v-if="star==0"  src="../../static/star3.png" alt="">
  		<span>{{item.average}}</span>
  	</div>
  		</div>
			
  </div>
</template>

<script>
import util from './../js/util.js' 
export default {
  props: ['post'],
	methods:{
		detail(pram){
			 this.$router.push({name:'detail', params:{id:pram}});
		}
	}
}
</script>

