<template>
  <div>
   <header>
   	<h1>C-TIME</h1>
   		<ul>
   				<li  v-for="item in title" @click="toggle(item)" v-bind:class="[{clickTab:currentTab==item}]">{{item}}</li>
   		</ul>	
   </header>
	   <component :is='currentTab'></component>
		 <foot></foot>
 </div>
</template>

<script>
	import  Movie from './movies'
	import  MV from './m_video.vue'
	import  foot from './foot.vue'
	import util from './../js/util.js' 
  import '@/assets/normalize.css'
  
export default {
  name: 'HelloWorld',
  data () {
    return {
      currentTab:'Movie',
      tab:'in_theaters',
      title:['Movie','MV']
    }
  },
	components:{
		Movie,
		MV,
		foot
	},
	methods:{
		toggle(item){
			this.currentTab=item;
			
		}
	},
	mounted(){
		 const nav = document.querySelector('header');
     let topOfNav = nav.offsetHeight;

    function fixNav() {
      if (window.scrollY >= topOfNav) {
        //document.body.style.paddingTop =0;
        nav.classList.add('fixed-nav');
      } else {
        nav.classList.remove('fixed-nav');
        //document.body.style.paddingTop = 0;
      }
    }

    window.addEventListener('scroll', fixNav);
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	body{
	    font-family: 'Open Sans', sans-serif;
			transition:all 0.5s;
	}
	
	header{
		  position: relative;
			top:0;
		  width: 100%;
		  transition:all 0.5s;
      z-index:9;
	    height:110px;
	    background:#3cb47f;
	    color: #fff;
	    display: flex;
			justify-content: space-between;
			
	}
	.fixed-nav{
		position: fixed;
		box-shadow:0 5px 0 rgba(0,0,0,0.1); 
	}
	header h1{
	    display: inline-block;
	    font-family: 'Shadows Into Light', cursive;
	    line-height: 100px;
	    text-indent: 100px;
	}
	header ul{
	    line-height: 100px;
	    margin-right: 10rem;
	}
	
	header ul li{
	   position: relative;
	    display: inline-block;
	    font-size: 20px;
	    margin-left:150px;
	    letter-spacing:5px;
	    width:120px;
	    text-align: center;
	    cursor: pointer;
	}
	header ul li:after{
	    content: '';
	    position: absolute;
	    width:120px;
	    height:2px;
	    background: #fff;
	    bottom: 0;
	    left: 0;
	    transform:scale(0);
	    transition:.5s ease-in-out;
	    transform-origin: 0 100%;
	}
	header ul li:hover:after{
	    transform:scale(1);
	    transform-origin: 0 0;
	}
	.clickTab{
	   color:red;
	}
</style>



