export default{

	convertToStarsArray(star){
			const array=[]; 	
			//2 1 0
			for(let i=10;i<=50;i+=10){
				if(i<=star){
					array.push(2)
				
				}else{
					array.push(0)
				}
				
			}
			if(star.substr(1)==5){
			  array.push(1)
			}
			if(array.length>5){
				array.forEach((item,index)=>{
					if(item==0){
						array.splice(index,1);
					}
				})
			}
				
			return array;	
		
	},
  convertToCastString(casts) {
      let  castsjoin= "";
      casts.forEach((item)=>{
				castsjoin+=item.name+" / ";
			})
			return castsjoin.substring(0, castsjoin.length-2);
   },
   convertToCastInfos(casts) {
     let castsArray = []
     casts.forEach((item)=>{
			 let cast={
				 img:item.avatars?item.avatars.small:"",
				 castname:item.name
			 }
			 castsArray.push(cast);
		 }) 
     return castsArray;
  },
	mvlist(){
		const isPlay = document.querySelector(".isPlay"); //► 暂停图标
    const video = document.querySelector("video");
    const fill = document.querySelector(".fill"); //填充的进度条
   const progress = document.querySelector(".progress"); //进度条
    const pause = document.querySelector(".pause"); //视频中间的暂停图标
//点击► 在DOM树上移除视频中间的暂停图标 
   pause.addEventListener('click', () => {
    document.querySelector(".video").removeChild(pause);
    //视频开始播放
    video.play();
    //改变图标
    isPlay.innerText = "❚ ❚";
});
   isPlay.addEventListener("click", () => {
        //是否视频中间的暂停图标点击了
        if (document.querySelector(".pause"))
            document.querySelector(".video").removeChild(pause);
        const method = video.paused ? 'play' : 'pause';
        video[method]();
        const icon = video.paused ? '►' : '❚ ❚';
        isPlay.innerText = icon;
    })
    //视频播放时改变进度条
   video.addEventListener("timeupdate", () => {
    const percent = (video.currentTime / video.duration) * 100;
    fill.style.flexBasis = `${percent}%`;
  });
//进度条拖拽
  let mousedown = false;

  function scrub(e) {
    const currentTime = (e.offsetX / progress.offsetWidth) * video.duration;
    video.currentTime = currentTime;
  }

  progress.addEventListener('mousemove', (e) => {
    mousedown && scrub(e);

  });
  progress.addEventListener('mousedown', () => {
    mousedown = true;
    if (document.querySelector(".pause")) {
        document.querySelector(".video").removeChild(pause);
    }
  });
  progress.addEventListener('mouseup', () => mousedown = false);
//视频结束
   video.addEventListener("ended", () => {
    isPlay.innerText = "►";
  });
//页面离开
  document.addEventListener("visibilitychange", () => {
        if (document.hidden) {
            video.pause();
            isPlay.innerText = "►";
        }
    }, false)
    //全屏
 const fullScreen = document.querySelector(".fullScreen");
//兼容浏览器 必须带前缀
  function launchFullScreen(element) {
    if (element.requestFullscreen) {
        element.requestFullscreen();
    } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
    } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
    } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen();
    }
}
//退出全屏
  function exitFullscreen() {
    if (document.exitFullscreen) {
        document.exitFullscreen();
    } else if (document.mozExitFullScreen) {
        document.mozExitFullScreen();
    } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
    }
}

// 点击了全屏图标
  fullScreen.addEventListener("click", () => launchFullScreen(video));
  document.addEventListener("webkitfullscreenchange", () => {
    if (document.webkitfullscreenElement !== null) {
        document.webkitfullscreenElement;
    }
  })
	}
	
 }

		 
		 
		


