<template>
	<div>
		<header>
		<input  type="text" placeholder="你好，之华、恐龙王" @blur="blur()" @focus="focus()" @keydown.delete="cancle()"/>
		<button @click='blur()'>搜索</button>
		</header>
		<div class="list">
			<movie v-if="movieShow" :post='movieArray'></movie>
			<movie v-if="searchShow"  :post='searchResult'></movie>
		</div>
	</div>
</template>

<script>
	
	import '@/assets/list.css'
	import movie from '@/components/movie'
	import util from './../js/util.js' 
	
	export default {
		name: 'movielist',
		components:{
			movie
		},
		data(){
			return {
				movieArray:[],
				requestUrl:'',
				searchResult:[],
				searchShow:false,
				movieShow:true
			}
		},
		mounted(){
			const name=this.$route.params.movieType;
			let url='';
			switch (name){
				case '正在热映':
				url='https://api.douban.com/v2/movie/in_theaters?count=12';
				break;
				case '即将热映':
				url='https://api.douban.com/v2/movie/coming_soon?count=12';
				break;
				case 'top250':
				url='https://api.douban.com/v2/movie/top250?count=12';
				break;
				default:
				url='https://api.douban.com/v2/movie/in_theaters?count=12';
				break;
			}
			this.requestUrl=url;
			this.getMovieData(url,'');
			this.blur();
		},
		methods:{
				getMovieData(url,key){
					let that=this;
					let movies=[];
					$.ajax({
					async:true,
					url:url,
					dataType:'jsonp',
					success(res, status, xhr){
							if(status=='success'){
								that.processDoubanData(res,key)
							}
					}
					})
				},
				processDoubanData(res,key){
					const movies=[];
						for(let idx in res.subjects){
							const subject=res.subjects[idx];
							let title = subject.title;
							if (title.length >6){
								title = title.substring(0, 6) + "...";
								}
							var temp = {
								stars:util.convertToStarsArray(subject.rating.stars),
								title: title,
								allTilte:subject.title,
								average: subject.rating.average,
								coverageUrl:subject.images.medium,
								movieId: subject.id
								}
								movies.push(temp);
						}
						this.movieArray=movies;
					    if(key){
							this.searchResult=movies;
						}			
				},
				focus(){
					this.movieShow=false;
					this.searchShow=true;
					
				},
				cancle(){
					this.movieShow=true;
					this.searchShow=false;
					
				},
				 blur(){
					
					const text=$('header input').val();
					
					let url='https://api.douban.com/v2/movie/search?q='+text;
					this.getMovieData(url,'search');
				}
				
		}
	}
</script>





