<template>
	  <div class="thirdly">
    <div class="wrap_video">
        <div class="video">
            <video  :poster="'../../static/img/'+post" :src="src"></video>
            <div class="controls">
                <span class="isPlay">►</span> <span class="progress">
             <span class="fill"></span></span>
                <span class="fullScreen"></span>
            </div>
            <img class="pause" src="static/btn.png" alt=""/>
        </div>
    </div>
</div>
</template>

<script>
	import axios from 'axios';
	import '@/assets/mvlist.css'
	import util from '../js/util.js';
	export default {
		data(){
			return{
				src:'',
				post:''
			}
		},
		mounted(){
			util.mvlist();
			const src=this.$route.params.src;
			const post=this.$route.params.post;
		
			this.post=post;
			this.src=src;
		}
	}
</script>





