<template>
	<div>
      <div class="detail">
    <div class="bread">
        <h1 class="title">{{movie.title}}<span>{{movie.country}} . {{movie.year}}</span></h1>
        <div class="right">
            <span class="num">{{movie.wishCount}}</span><span class="world">人喜欢</span>
            <span class="num">{{movie.commentCount}}</span><span class="world">条评论</span>
        </div>
    </div>
    <div class="mask">
        <img v-lazy="movie.movieImg" alt=""/>
        <div class="blur"></div>
    </div>
    <div class="cover">
        <img  v-lazy="movie.movieImg" alt=""/>
    </div>
    <div class="info">
        <p>
            <span class="key">评分</span>
            <span>
             <img  v-for="star in movie.star" v-if="star==2"  src="../../static/star1.png" alt="">
             <img v-for="star in movie.star" v-if="star==1"  src="../../static/star2.png" alt="">
             <img v-for="star in movie.star" v-if="star==0"  src="../../static/star3.png" alt="">
            </span>
            <span class="word">{{movie.score}}</span>
        </p>
        <p> <span class="key">导演</span> <span class="word">{{director.d_name}}</span></p>
        <p> <span class="key">影人</span> <span class="word">{{movie.casts}}</span></p>
        <p> <span class="key">类型</span> <span class="word">{{movie.generes}}</span></p>
    </div>
    <div class="summay">
        <p>剧情简介</p>
        <p>{{movie.summary}}</p>
    </div>
    <div class="casts">
        <p>影人</p>
        <div class="cast">
            <div class="castList">
                <img v-lazy="director.avatar" alt="..."/>
               <span>{{director.d_name}}</span>
                <span>导演</span>
            </div>
            <div class="castList" v-for="item in castsInfo">
                <img  v-lazy="item.img" alt="..."/>
               <span>{{item.castname}}</span>
                <span>演员</span>
            </div>
        </div>
    </div>
</div> 
	</div>
</template>

<script>
	 import '@/assets/detail.css'
	 import util from '../js/util.js'
	export default {
		data(){
			return {
			 movie:"",
			 castsInfo:"",
			 director:""
			}
		},created(){
			const id=this.$route.params.id;
			let url='https://api.douban.com/v2/movie/subject/'+id;
			this.getMovieData(url);	
		},methods:{
			getMovieData(url){
							let that=this;
						
							$.ajax({
							async:true,
							url:url,
							dataType:'jsonp',
							success(res, status, xhr){
									if(status=='success'){
										const	castsInfo=util.convertToCastInfos(res.casts);
									  that.processDoubanData(res)
									}
							}
							})
							
							},
						processDoubanData(data){
							
							let movie={
								movieImg: data.images ? data.images.large : "",
						    country: data.countries[0],
						    title: data.title,
						    wishCount: data.wish_count,
						    commentCount: data.comments_count,
						    year: data.year,
						    generes: data.genres.join("、"),
								star:util.convertToStarsArray(data.rating.stars),
								score: data.rating.average,
								summary:data.summary,
								casts: util.convertToCastString(data.casts),
								year: data.year
							}
								let director = {
										avatar: "",
										d_name: "",
										id: ""
										};
									if (data.directors[0] != null) {
								if (data.directors[0].avatars != null) {
									director.avatar = data.directors[0].avatars.small;
									
								   }
								director.id = data.directors[0].id;
								director.d_name = data.directors[0].name;
							}
						    
						  const	castsInfo=util.convertToCastInfos(data.casts);
						  console.log(castsInfo)
						  this.castsInfo=castsInfo;
							this.director=director;
							this.movie=movie;
						}
						
						
	 	   }
	   }
</script>




