<template>
	<section class="m_playing">
		<bread :title='inTheaters.categoryTitle'></bread>
		<movie :post='inTheaters.movie'></movie>
		<bread :title='comingSoon.categoryTitle'></bread>
		<movie :post='comingSoon.movie'></movie>
	<bread :title='top250.categoryTitle'></bread>
	<movie :post='top250.movie'></movie>
	
</section>
</template>

<script>
	import movie from './movie'
	import bread from './bread'
	import util from './../js/util.js'
	import '@/assets/movies.css'
	export default {
		components:{
			movie,
			bread
		},
		data(){
			return{
			 inTheaters: {},
			 comingSoon: {},
			 top250:{}
		}
		},
		mounted(){
		  this.getMovieData('https://api.douban.com/v2/movie/in_theaters?count=6','inTheaters','正在热映');
			this.getMovieData('https://api.douban.com/v2/movie/coming_soon?count=6','comingSoon','即将热映');
      this.getMovieData('https://api.douban.com/v2/movie/top250?count=6','top250','top250');
			 
	  },
	methods:{
	getMovieData(url,keywords,categoryTitle){
		let that=this;
		let movies=[];
		$.ajax({
		async:true,
		url:url,
		dataType:'jsonp',
		success(res, status, xhr){
				if(status=='success'){
					that.processDoubanData(res,keywords,categoryTitle)
				}
		}
		})
	},
	processDoubanData(res,keywords,categoryTitle){
		const movies=[];
			for(let idx in res.subjects){
				const subject=res.subjects[idx];
				let title = subject.title;
				if (title.length > 6){
					title = title.substring(0, 6) + "...";
					}
				var temp = {
					stars:util.convertToStarsArray(subject.rating.stars),
					title: title,
					allTilte:subject.title,
					average: subject.rating.average,
					coverageUrl:subject.images.small,
					movieId: subject.id
					}
					movies.push(temp);
			}
				switch (keywords){
					case 'inTheaters':
					 this.inTheaters={
						movie:movies,
						categoryTitle:categoryTitle
					}
					break;
					case 'comingSoon':
					this.comingSoon={
						movie:movies,
						categoryTitle:categoryTitle
					}
					break;
					case 'top250':
					this.top250={
						movie:movies,
						categoryTitle:categoryTitle
					}
					break;
				}
				
				
	}
		
	}
}

</script>




